* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 100; }

p {
  margin-top: 1rem; }

button {
  background: none;
  font-size: 1.5rem; }

li {
  list-style: none; }

ul {
  list-style-type: none; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Courier New', Courier, monospace; }

h2, h3, h4, h5, h6 {
  margin-bottom: 1rem; }

form {
  width: 90%; }

input[type="text"], textarea, input[type="email"] {
  font-family: 'Courier New', Courier, monospace;
  color: black;
  font-size: 1rem;
  width: 100%;
  border-left: none;
  border-top: none;
  border-right: none;
  border-bottom: solid 1px #b3aeae;
  margin: 1rem; }

input[type="text"]:focus, textarea:focus, input:focus[type="email"] {
  outline: none;
  border-color: black;
  animation: textBoxClicked 5s;
  -moz-animation: textBoxClicked 1s;
  -webkit-animation: textBoxClicked 1s;
  -o-animation: textBoxClicked 1s; }

textarea {
  width: 100%;
  border-bottom: solid 1px #b3aeae;
  border-right: solid 1px #b3aeae; }

@keyframes textBoxClicked {
  from {
    border-color: #b3aeae; }
  to {
    border-color: black; } }

.navbarList {
  display: flex;
  width: 100%; }
  .navbarList button {
    flex-grow: 1;
    cursor: pointer;
    position: relative;
    padding: 0.8rem
 1rem
 1rem;
    border: 0.125rem solid black;
    color: #e7e7e7;
    background-color: black;
    font-size: 1.5rem;
    text-transform: lowercase;
    text-shadow: rgba(0, 0, 0, 0.1) 2px 2px;
    transition: flex-grow 250ms cubic-bezier(0.215, 0.61, 0.355, 1); }
    .navbarList button:hover, .navbarList button:focus {
      flex-grow: 2;
      color: black;
      outline: none;
      text-shadow: none;
      background-color: #e7e7e7; }
  .navbarList button + button {
    border-left: 0.125rem solid black;
    margin-left: calc(0.125rem * -1); }
  .navbarList button:active {
    transform: translateY(0.125rem); }

.navbarList:hover button:focus:not(:hover) {
  flex-grow: 1;
  color: #e7e7e7;
  background-color: black;
  outline-color: #593131; }

.navButtonActive {
  flex-grow: 2;
  color: black;
  outline: none;
  text-shadow: none;
  background-color: #e7e7e7; }

header {
  background-color: #dedede; }

#nameCanvas {
  position: inherit; }

.myName {
  color: #a3a3a3;
  font-family: 'Courier New', Courier, monospace;
  padding-right: 1rem; }

.card {
  display: flex;
  flex-direction: row;
  padding: 0;
  background-color: #e7e7e7;
  margin: 1rem; }

@media (max-width: 1128px) {
  .card {
    flex-direction: column;
    align-items: center; } }

.flexColumn, form, .flexColumn--wrap, .flexColumn--noPadding, .techCol, .contactContainer {
  display: flex;
  flex-direction: column;
  padding: 2rem; }
  .flexColumn--wrap {
    flex-wrap: wrap; }
  .flexColumn--noPadding {
    padding: 0; }

.flexRow, header, .flexRow--wrap, .flexRow--noPadding, .techRow {
  display: flex;
  flex-direction: row;
  padding: 2rem; }
  .flexRow--wrap {
    flex-wrap: wrap; }
  .flexRow--noPadding {
    padding: 0; }

.techCol li {
  display: flex;
  align-items: center; }
  .techCol li img {
    width: 24px;
    height: 24px;
    margin-right: 0.5rem; }

.techRow {
  justify-content: space-around; }

@media (max-width: 692px) {
  .techRow {
    flex-direction: column; } }

.submitButton {
  flex-grow: 1;
  cursor: pointer;
  position: relative;
  padding: 0.8rem
 1rem
 1rem;
  border: 0.125rem solid black;
  color: #e7e7e7;
  background-color: black;
  font-size: 1.5rem;
  text-transform: lowercase;
  text-shadow: rgba(0, 0, 0, 0.1) 2px 2px;
  transition: flex-grow 250ms cubic-bezier(0.215, 0.61, 0.355, 1);
  width: 100%; }
  .submitButton:hover, .submitButton:focus {
    flex-grow: 2;
    color: black;
    outline: none;
    text-shadow: none;
    background-color: #e7e7e7; }

.contactContainer {
  align-items: center; }
